.program-selection {
  @include tablet {
    padding: 0 40px;
  }

  @include desktop {
    padding: 0 50px;
  }
}

.program-selection__title {
  text-align: start;
}

.program-selection__discription {
  font-family: $main-font;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  text-transform: uppercase;
}

.program-selection__pet-data {
  @include reset-indents;
}

.program-selection__target {
  @include reset-indents;
}

.program-selection__owner-data {
  @include reset-indents;
}

.program-selection__comment {
  @include reset-indents;
}

.program-selection__additionally {
  @include reset-indents;
}

.program-selection__container {
  @include tablet {
    display: flex;
  }
}

.program-selection__button {
  padding: 7px 44px;
}

.program-selection__legend {
  font-size: 14px;
  line-height: 16px;
  text-align: center;
}
